import { ossPrefix } from '@/config/index'
import springFestivalApis from '@/store/apis/sfFestival'
import wechat from '@/utils/wechat' 
export default {
  data () {
    return {
      ossPrefix: `${ossPrefix}shareActivities/`,
      showShareModal: false,
      showCouponModal: false,
      couponDetail: {
        "icon_url":"",
        "name": ""
      },
      ShareStatus: {
        FINISHED: 0, // 已经发放完毕了
        SUCCESS: 1, // 分享成功
        SHARED: 2, // 已经分享过一次了
      }
    }
  },
  methods: {
    doPopShareModal () {
      this.showShareModal = true
    },
    doHideShareModal () {
      this.showShareModal = false
    },
    doPopCouponModal (coupon) {
      this.couponDetail = coupon
      this.showCouponModal = true
    },
    doHideCouponModal() {
      this.showCouponModal = false
    },
    toCoupons () {
      this.$router.push('/coupons')
    },
    $_dealResult (res) {
      const { status, coupon } = res.data
      const finishedTips = '红包发完了，参加春节活动更多大礼等你拿哦'
      const sharedTips = '每个用户只能领取一次哦，参加春节活动更多大礼等你拿！'
      switch (status) {
        case this.ShareStatus.FINISHED: this.$vux.toast.text(finishedTips, 'middle'); break;
        case this.ShareStatus.SUCCESS: this.doPopCouponModal(coupon); break;
        case this.ShareStatus.SHARED: this.$vux.toast.text(finishedTips, 'middle'); break;
        default: break;
      }
    },
    $_initWechatShare () {
      wechat.initSmartShare({
        title: '和+PLUS 新春福利大派送',
        desc: '数量有限 送完为止'
      }).then(res => {
        springFestivalApis.getRewardByShare().then(res => {
          this.$_dealResult(res)
        })
      }).catch(err => {
        this.$vux.toast.text('分享出错了', 'bottom')
      })
    }
  },
  created () {
    this.$_initWechatShare()
  }
}
